import TrailsSprite from "./base/TrailsSprite";
import BallModule, { heroVo } from "modules/dataModule/BallModule";
import Game from "xengine/game/Game";
import { path } from "xengine/utils/path";
import { ParabolaFunc, Parabola } from "modules/func/ParabolaFunc";
import { BallScene } from "./BallScene";
import Prefab_defen_01 from "view/raw/3d/Sprite3D_defen_01";
import { angleByPoint, pointByAngle, direByAngle } from "xengine/utils/math";
import { ColliderEvents } from "./base/ColliderTrigger";
import { random_range } from "xengine/utils";
import Prefab_LuoDiYan from "view/raw/3d/Sprite3D_LuoDiYan";
import XSprite3D from "xengine/view/3d/XSprite3D.laya";

export class Ball extends TrailsSprite {
    private _ballModule: BallModule;

    private _rigidBody: Laya.Rigidbody3D;
    private _colliderShape: Laya.SphereColliderShape;

    private _tween: Parabola;
    private _flyComCallBack: Laya.Handler;
    private _collisionPanelNum: number = 0; // 撞击地面的次数

    // 是否进球
    public goalBool: boolean = false;
    public ballData: ballData;

    public isHollow: boolean = true; // 是否是空心
    // 是否使用过
    public used: boolean = false;

    // 可能以后会伴随 技能  暂时只认皮肤
    initData(ballData: ballData) {
        this._ballModule = Game.inst.get_module<BallModule>(BallModule);
        this.ballData = ballData;
        this.used = false;
        this.url = path.res3DUrlByName(this._ballModule.futureResUrl, ballData.skin, false);
        console.log(this.url);
    }

    // 游戏开始
    public async startGame(parent?: Laya.Sprite3D | Laya.Scene3D) {
        // 打开碰撞检测
        this.offEvent();
        if (parent) {
            parent.addChild(this.displayObject);
        }
        this._rigidBody = this.trailNode.getComponent(Laya.Rigidbody3D);
        this._rigidBody.restitution = this._ballModule.ballForce;
        this._rigidBody.mass = 1;
        this.setRigidBody(false);
    }

    // 第一种 实时改变位置 返回是否成功
    public fly(angle: number, comCallBack: Laya.Handler): boolean {
        // 没有角度 代表 虚拟玩家投的
        this.onEvent();
        this.used = true;
        this._flyComCallBack = comCallBack;
        let currectAngle = this.getCorrectAngle();
        console.log("操控方向" + angle + "   正确方向" + currectAngle);
        let isSuc = angle == null || Math.abs(currectAngle - angle) <= this._ballModule.angleOffset;
        let endPos: Laya.Vector3 = this.getEndPos(isSuc, angle)
        let startPos = this.trailNode.transform.position.clone();
        let playInitHit = angle == null ? this._ballModule.enemyInitHit : this._ballModule.playInitHit;
        let boardRadius = this._ballModule.boardRadius;
        let temp = random_range(0, 1 - playInitHit);
        this._tween = ParabolaFunc.instance.startPairingParabola(this.trailNode, startPos, endPos, Laya.Handler.create(this, () => {
            this.tweenEndThen();
        }), this.getCenterPos(startPos, endPos), boardRadius * 2 * temp);
        this._tween.time = 1000;
        return isSuc;
    }

    // 飞向一个预测的点
    public flyFuture(angle: number) {
        // 提前一个点 在篮板之前 跟篮板一起 运动
    }

    // 触发器 不会产生 实际接触点
    onTriggerEnter(event: Laya.PhysicsComponent) {
        // console.log("触发器");
        if (event.owner) {
            this.tweenEndThen();
            let collName = event.owner.name;
            this.collisionAndThen(collName);
        }
    }

    // 碰撞体 会产生 实际接触点 但是会慢几帧
    onCollisionEnter(collision: Laya.Collision): void {
        // console.log("碰撞体");
        let collName = collision.other.owner.name;
        this.collisionAndThen(collName);
    }

    public getEndPos(isEndPos: boolean, angle: number) {
        if (isEndPos) {
            return BallScene.inst.board.trailNode.transform.position;
        }
        else {
            let temp = this.trailNode.transform.position;
            let pos = BallScene.inst.board.trailNode.transform.position;
            let direPos = direByAngle({ x: temp.x, y: temp.z }, angle);
            let disTimes = Math.abs(temp.z) * 0.7;
            // laya x坐标系是反的
            return new Laya.Vector3(temp.x + -direPos.x * disTimes, pos.y, temp.z + direPos.y * disTimes);
        }
    }

    dispose() {
        console.log("消失");
        BallScene.inst.playBallDis(this.trailNode.transform.position.clone());
        ParabolaFunc.instance.removeParabola(this._tween);
        Laya.timer.clearAll(this);
        this.setRigidBody(false);
        if (this.displayObject && this.displayObject.parent) {
            this.displayObject.parent.removeChild(this.displayObject);
        }
        super.dispose();
        this.goalBool = false;
        this._flyComCallBack = null;
        this._tween = null;
        this.isHollow = true;
        this.used = false;
        this._rigidBody = null;
        this._collisionPanelNum = 0;
        this._colliderShape = null;
        Laya.Pool.recover("ball", this);
    }

    private checkCanDis(bool: boolean = false) {
        // 撞到两次地板球消失
        if (this._collisionPanelNum == 1) {
            Laya.timer.once(2000, this, () => {
                this.shootResult(false);
                this.dispose();
            });
        }
        // 待改 LIUTODO
        if (this._collisionPanelNum >= 2 || bool) {
            this.shootResult(false);
            this.dispose();
        }
    }

    private collisionAndThen(collName) {
        let bModule = this._ballModule;
        if (collName == bModule.panelName) {
            this._collisionPanelNum++;
            this.checkCanDis();
        }
        else if (collName == bModule.goalName) {
            this.shootResult(true);
        }
        else if (collName == bModule.boardName) {
            this.hollow = false;
        }
        else if (collName == bModule.baffleName) {
        }
        else if (collName == bModule.lanqiuName) {
        }
        else {
            this._collisionPanelNum++;
            this.checkCanDis();
        }
    }

    // 暂时 定的规则 球到地 或者 球进了
    private shootResult(bool: boolean) {
        // // 取消监听
        // this.offEvent();
        this.goalBool = this.goalBool || bool;
        if (this._flyComCallBack && this._flyComCallBack.caller) {
            this._flyComCallBack.runWith(bool);
            this._flyComCallBack = null;
        }
    }

    // 设置是否是空心  (撞到篮板 为非空心)
    private set hollow(value: boolean) {
        this.isHollow = !this.isHollow ? this.isHollow : value;
    }

    private tweenEndThen() {
        if (this._tween) {
            let force = this._tween.forceDire;
            let forceNum = this._ballModule.onceForce;
            ParabolaFunc.instance.removeParabola(this._tween);
            Laya.Vector3.scale(force, forceNum, force)
            this._rigidBody.applyForce(force);
            this.setRigidBody(true);
            this._tween = null;
        }
    }

    private getCorrectAngle(future: boolean = false): number {
        if (!future) {
            let startPos = this.trailNode.transform.position.clone();
            let endPos = BallScene.inst.board.trailNode.transform.position;
            // laya坐标系是反的
            return angleByPoint(startPos, endPos);
        }
    }

    // 返回抛物线中心点
    private getCenterPos(startPos: Laya.Vector3, endPos: Laya.Vector3): Laya.Vector3 {
        // 暂时写一个 点 LIUTODO
        let temp = new Laya.Vector3();
        let zOffset = endPos.z - startPos.z;
        temp.x = 0;
        temp.y = endPos.y * 2 + zOffset / 3;
        temp.z = startPos.z + 0.5 * zOffset;
        console.log(temp);
        return temp;
    }

    private setRigidBody(bool: boolean) {
        if (!this._rigidBody) {
            console.log("bug")
        }
        this._rigidBody.isKinematic = !bool;
        this._rigidBody.isTrigger = !bool;
    }
}