import Module, { game_module } from "xengine/game/Module";
import Game from "xengine/game/Game";
import GameStaticData from "./GameStaticData";
import { getVector3ByStr } from "xengine/utils/math";
import { relative } from "path";
import XEngine from "xengine/XEngine";
import { path } from "xengine/utils/path";
import { Ball } from "view/3d/Ball";

@game_module('BallModule')
export default class BallModule extends Module {
	public staticData: GameStaticData;
	public levelVos: LevelVo[] = [];

	public level: number = 1;

	public futureResUrl: string = "futureRes";

	public panelName: string = "panel";
	public goalName: string = "goalCube";
	public boardName: string = "boardModel";
	public baffleName: string = "baffle";
	public lanqiuName: string = "lanqiu";

	// 玩家的英雄ID
	public heroId: number = 3000;
	// 玩家的篮球ID
	public ballId: number = 4000;
	public touchDisShoot: number = 50;
	public onceForce: number = 2000;

	public panelForce: number = 0.5;
	public baffleForce: number = 0.5;
	public boardForce: number = 0.5;
	public ballForce: number = 1;
	public angleOffset: number = 30;

	public playShootTimeMin: number = 1;
	public playShootTimeMax: number = 7;

	public playHitAdd: number = 0.05;

	public allShootNum: number = 4;
	public playInitHit: number = 1;
	public enemyInitHit: number = 0.5;

	boardRadius: number = 0.55; // 篮板半径

	/** 初始化 */
	public initialize() {
		this.staticData = Game.inst.get_module<GameStaticData>(GameStaticData);
	}

	/** 逻辑更新 */
	public update(dt: number) { }

	/** 模块开始 */
	public start() {
		// 初始化本关卡信息
		this.touchDisShoot = this.staticData.get_config_by_id(5000).value;
		this.onceForce = this.staticData.get_config_by_id(5001).value;
		this.panelForce = this.staticData.get_config_by_id(5002).value;
		this.baffleForce = this.staticData.get_config_by_id(5003).value;
		this.boardForce = this.staticData.get_config_by_id(5004).value;
		this.ballForce = this.staticData.get_config_by_id(5005).value;
		this.angleOffset = this.staticData.get_config_by_id(5006).value;
		this.playShootTimeMin = this.staticData.get_config_by_id(5007).value;
		this.playShootTimeMax = this.staticData.get_config_by_id(5008).value;
		this.playHitAdd = this.staticData.get_config_by_id(5009).value;
		this.allShootNum = this.staticData.get_config_by_id(5013).value;
		this.enemyInitHit = this.staticData.get_config_by_id(5014).value;
		this.playInitHit = this.staticData.get_config_by_id(5015).value;
	}

	// 预加载下一关
	public preloadNextLv() {
		let nextLevelVo = this.nextLevelVo();
		let nowLevelVo = this.nextLevelVo();
		let skinName = nextLevelVo.levelData.skin;
		if (nowLevelVo.levelData.skin == skinName) {
			return;
		}
		XEngine.inst.res.load(path.res3DUrlByName(skinName, skinName));
	}

	// 设置下一关
	public lvGoOn() {
		this.level++;
	}

	public updateLevelData() {
		let levelVo: LevelVo = this.levelVoByLv(this.level);
	}

	levelVoByLv(level: number): LevelVo {
		return this.levelVos[level] || (this.levelVos[level] = new LevelVo(level, this.heroId, this.ballId));
	}

	nowLevelVo(): LevelVo {
		return this.levelVoByLv(this.level);
	}

	nextLevelVo(): LevelVo {
		return this.levelVoByLv(this.level + 1);
	}

	/** 读档 */
	public load(data: any) {
		this.level = data.level || this.level;
		this.heroId = data.heroId || this.heroId;
		this.ballId = data.ballId || this.ballId;
	}

	/** 存档 */
	public save() {
		return { level: this.level, heroId: this.heroId, ballId: this.ballId };
	}
}

// 关卡vo
export class LevelVo {
	levelData: LevelData;
	bafflesVo: baffleVo[];
	boardVo: boardVo;
	herosVo: heroVo[];
	ballsData: ballData[];

	/**
	 * @param lv 等级
	 * @param heroId 英雄id
	 * @param ballId 篮球id
	 */
	constructor(lv: number, heroId: number, ballId: number) {
		let gamedata = Game.inst.get_module<GameStaticData>(GameStaticData);
		this.levelData = gamedata.get_level_data(lv);
		let baffles = this.levelData.baffles ? this.levelData.baffles.split(",") : [];
		let heros = this.levelData.otherHeros ? this.levelData.otherHeros.split(",") : [];
		let balls = this.levelData.otherBalls ? this.levelData.otherBalls.split(",") : [];
		this.bafflesVo = [];
		this.herosVo = [];
		this.ballsData = [];
		baffles.forEach((id: string, index: number) => {
			if (id)
				this.bafflesVo[index] = new baffleVo(gamedata.get_config_by_id(id));
		})
		this.herosVo[0] = new heroVo(gamedata.get_config_by_id(heroId), EHreoIndex.CENTER, true);
		heros.forEach((id: string, index: number) => {
			if (id)
				this.herosVo[index + 1] = new heroVo(gamedata.get_config_by_id(id), index + 1);
		})
		this.ballsData[0] = gamedata.get_config_by_id(ballId);
		balls.forEach((id: string, index: number) => {
			if (id)
				this.ballsData[index + 1] = gamedata.get_config_by_id(id);
		})
		// 现在支持  一个篮板
		let boards = this.levelData.boards.split(",")[0];
		this.boardVo = new boardVo(gamedata.get_config_by_id(boards));
	}
}

export class hasTrailVo {
	trailVos: trailVo[];
	constructor(data: any) {
		if (data && data.trails) {
			let trails = data.trails.split(",");
			this.trailVos = [];
			let gamedata = Game.inst.get_module<GameStaticData>(GameStaticData);
			trails.forEach((id: string, index: number) => {
				if (id)
					this.trailVos[index] = new trailVo(gamedata.get_config_by_id(id));
			})
		}
	}
}

// 阻挡面板(阻挡物)vo
export class baffleVo extends hasTrailVo {
	baffleData: baffleData;

	constructor(baffleData: baffleData) {
		super(baffleData);
		this.baffleData = baffleData;
	}
}

// 人物vo
export class heroVo extends hasTrailVo {
	heroData: heroData;
	isPlayer: boolean;
	index: number;
	// hero 的运动也需配置
	constructor(heroData: heroData, index: EHreoIndex, isPlayer: boolean = false) {
		super(heroData);
		this.index = index;
		this.heroData = heroData;
		this.isPlayer = isPlayer;
		this.trailVos = [];
		let gamedata = Game.inst.get_module<GameStaticData>(GameStaticData);
		let data = gamedata.get_config_by_id(5010 + index);
		let trails = data.value.split(",");
		trails.forEach((id: string, index: number) => {
			if (id)
				this.trailVos[index] = new trailVo(gamedata.get_config_by_id(id));
		})
	}
}

// 篮板vo
export class boardVo extends hasTrailVo {
	boardData: boardData;

	constructor(boardData: boardData) {
		super(boardData);
		this.boardData = boardData;
	}
}

export enum ETrailType {
	LINE = 0,
	ROTATE = 1,
	STOP = 2,
	CIRCLE = 3
}

export enum EHreoIndex {
	LIFT = 1,
	CENTER = 0,
	RIGHT = 2
}

export class trailVo {
	trailData: trailData; // 表的数据
	private _startPos: Laya.Vector3;
	private _endPos: Laya.Vector3;
	private _anchor: Laya.Vector3;
	private _rotate: Laya.Vector3;
	ease: Function;

	constructor(data: trailData) {
		this.trailData = data;
		if (data.ease) {
			switch (data.ease) {
				case 1:
					this.ease = Laya.Ease.backIn; // 暂时  需要细调
					break;
			}
		}
		else
			this.ease = null;
	}

	// 需要旋转的角度
	get rotate(): Laya.Vector3 {
		let temp = this.trailData;
		return this._rotate || (this._rotate = new Laya.Vector3(temp.rotateX, temp.rotateY, temp.rotatez));
	}

	get startPos(): Laya.Vector3 {
		return this._startPos || (this._startPos = getVector3ByStr(this.trailData.startPos));
	}

	get endPos() {
		return this._endPos || (this._endPos = getVector3ByStr(this.trailData.endPos));
	}

	get anchor() {
		return this._anchor || (this._anchor = getVector3ByStr(this.trailData.anchor));
	}

	get speed() {
		return this.trailData.speed;
	}

	set speed(value: number) {
		this.trailData.speed = value;
	}
}