import Game from "xengine/game/Game";
import UI_GameOverView from "view/raw/Main/UI_GameOverView";
import MainScene from "./MainScene";
import UI_ProView from "view/raw/Main/UI_ProView";
import { BallScene, EEventType } from "view/3d/BallScene";
import Hero from "view/3d/Hero";
import { EHreoIndex } from "modules/dataModule/BallModule";
import UI_smallProCom from "view/raw/Main/UI_smallProCom";

/** This is an automatically generated class by FairyGUI. Please do not modify it. **/

export default class ProView extends UI_ProView {

    public static createInstance(): ProView {
        return <ProView><any>(fairygui.UIPackage.createObject("Main", "ProView"));
    }

    constructFromResource() {
        super.constructFromResource();
        BallScene.inst.on(EEventType.PROCHANGE, this, this.updatePro)
    }

    initPro() {
        this.initSmallProCom(this.m_leftGp);
        this.initSmallProCom(this.m_centerGp);
        this.initSmallProCom(this.m_rightGp);
    }

    private initSmallProCom(com: UI_smallProCom) {
        if (com && com._children) {
            com._children.forEach((child: fairygui.GImage) => {
                child.color = "#FFFFFF";
            })
        }
    }

    updatePro() {
        let heros = BallScene.inst.heros;
        heros.forEach((hero: Hero) => {
            if (hero) {
                let temp: UI_smallProCom;
                let color: string;
                switch (hero.heroVo.index) {
                    case EHreoIndex.LIFT:
                        temp = this.m_leftGp;
                        color = "#38e934";
                        break;
                    case EHreoIndex.CENTER:
                        temp = this.m_centerGp;
                        color = "#FFb22c";
                        break;
                    case EHreoIndex.RIGHT:
                        temp = this.m_rightGp;
                        color = "#00bbFF";
                        break;
                }
                let pro = hero.levelNum;
                for (let i = 0; i <= pro; i++) {
                    (temp._children[i] as fairygui.GImage).color = color;
                }
            }
        })
    }

    onNext() {
        MainScene.inst.startGame();
    }
}