import BallModule, { baffleVo, heroVo, EHreoIndex } from "modules/dataModule/BallModule";
import Prefab_defen_01 from "view/raw/3d/Sprite3D_defen_01";
import X3D from "xengine/view/3d/X3D";
import { path } from "xengine/utils/path";
import Trails from "./Trail";
import TrailsSprite from "./base/TrailsSprite";
import Game from "xengine/game/Game";
import { Ball } from "./Ball";
import { BallScene } from "./BallScene";
import { random_range } from "xengine/utils";
import Prefab_hero1 from "view/raw/3d/Sprite3D_hero1";
import { ParabolaFunc, Parabola } from "modules/func/ParabolaFunc";

export enum EHeroState {
    FAIL = "fail",
    WIN = "win",
    WALK = "walk",
    SHOOT = "shoot",
    IDLE = "idle_2",
    IDLE_WAIT = "idle_1",
}

// 面板  也可作为 障碍物
export default class Hero extends Prefab_hero1 {
    private _ballModule: BallModule;
    private _ballData: ballData;
    // 当前第几关  （大致关）
    public levelNum: number = -1;
    private _ball: Ball;
    public goalBool: boolean = false;
    public heroVo: heroVo;
    public scene: BallScene;
    private heroState: string;
    private _tween: Parabola;

    // 可能以后会伴随 技能  暂时只认皮肤
    initData(heroVo: heroVo, ballData: ballData, scene: BallScene) {
        this._ballModule = Game.inst.get_module<BallModule>(BallModule);
        this.scene = scene;
        this.heroVo = heroVo;
        this._ballData = ballData;
        this.url = path.res3DUrlByName(this._ballModule.futureResUrl, heroVo.heroData.skin, false);
    }

    async instance() {
        const node = await super.instance();
        this._ball = await this.getBall();
        this.cAnimator_juese = this.trailNode.getComponent(Laya.Animator) as Laya.Animator;
        this.trails.initData(this.heroVo.trailVos, this);
        return node;
    }

    // 游戏开始
    public async startGame() {
        if (this._ball && !this._ball.used && this._ball.displayObject) { }
        else
            this._ball = await this.getBall();
        this._ball.onEvent();
        this._ball.startGame(this.scene.displayObject);
        // 非玩家自动投  玩家自投
        this.setHeroState(EHeroState.IDLE_WAIT);
        Laya.timer.frameOnce(1, this, this.initBallPos)
    }

    public setHeroState(state: EHeroState) {
        if (this.heroState == state) return;
        this.heroState = state;
        this.cAnimator_juese.play(state);
        if (state == EHeroState.WIN || state == EHeroState.FAIL) {
            Laya.timer.clear(this, this.shoot);
        }
    }

    public autoShoot() {
        if (!this.heroVo.isPlayer) {
            let min = this._ballModule.playShootTimeMin;
            let max = this._ballModule.playShootTimeMax;
            let shootTime = random_range(min, max);
            Laya.timer.once(shootTime * 1000, this, this.shoot);
        }
    }

    dispose() {
        ParabolaFunc.instance.removeParabola(this._tween);
        this._tween = null;
        Laya.timer.clearAll(this);
        super.dispose();
    }

    // 投篮
    public shoot(angle: number) {
        // 当前球已被使用
        if (this._ball.used) return;
        this.scene.displayObject.addChild(this._ball.displayObject);
        this.setHeroState(EHeroState.SHOOT);
        // 就是最后一波            Laya.timer.clear(this, this.initBallPos);
        let bool = this._ball.fly(angle, Laya.Handler.create(this, (bool: boolean) => {
            this.goalBool = bool;
            if (bool) {
                this.levelNum++;
                this.heroVo.isPlayer && this.scene.playLihuaAndGood();
            }
            if (this.levelNum >= (this._ballModule.allShootNum - 1) && bool) {
                this.scene.gameOver(this);
                !this.heroVo.isPlayer && this.scene.cameraToHeroAndResult();
            }
            else
                this.scene.shootProAdd();
        }));
        // 摄像机镜头跟随 篮球
        if ((this.levelNum == this._ballModule.allShootNum - 2) && bool && this.heroVo.isPlayer) {
            let endPos = this._ball.getEndPos(true, null).clone();
            Laya.Vector3.add(endPos, new Laya.Vector3(0, 2, -5), endPos);
            let startPos = this.scene.camera.transform.position.clone();
            this._tween = ParabolaFunc.instance.startPairingParabola(this.scene.camera, startPos, endPos, Laya.Handler.create(this, () => {
                this.scene.cameraToHeroAndResult();
            }), this.getBallCenterPos(startPos, endPos));
            this._tween.targetRotationChange(false);
        }
        // 投球时间 150 
    }

    // 本轮投射结束开始 结算
    public shootEnd() {
        if (this.goalBool) {
            this.backOne();
        }
        else {
            this.scene.endProAdd();
        }
    }

    // 播放人物后退动作
    public backOne() {
        this.setHeroState(EHeroState.WALK);
        this.trails.startRun(false, Laya.Handler.create(this, () => {
            this.scene.endProAdd();
        }), this.levelNum);
    }

    private async getBall() {
        let ball: Ball = Laya.Pool.getItemByClass("ball", Ball);
        ball.initData(this._ballData);
        await ball.instance();
        return ball;
    }

    // 返回篮球抛物线中心点
    private getBallCenterPos(startPos: Laya.Vector3, endPos: Laya.Vector3): Laya.Vector3 {
        // 暂时写一个 点 LIUTODO
        let temp = new Laya.Vector3();
        temp.x = 0;
        temp.y = endPos.y * 2;
        temp.z = (startPos.z + endPos.z) / 2;
        console.log(temp);
        return temp;
    }

    private initBallPos() {
        // this._ball.trailNode.transform.position = this.juese_Bip001_Bip001_Pelvis_Bip001_Spine_Bip001_Spine1_Bip001_Neck_Bip001_Head_Point002.transform.position;
        // this.cAnimator_juese.linkSprite3DToAvatarNode("Point001",this._ball.trailNode);
        this.juese_Bip001_Bip001_Pelvis_Bip001_Spine_Bip001_Spine1_Bip001_Neck_Bip001_R_Clavicle_Bip001_R_UpperArm_Bip001_R_Forearm_Bip001_R_Hand_Point001.addChild(this._ball.displayObject);
        this._ball.displayObject.transform.position = this.juese_Bip001_Bip001_Pelvis_Bip001_Spine_Bip001_Spine1_Bip001_Neck_Bip001_R_Clavicle_Bip001_R_UpperArm_Bip001_R_Forearm_Bip001_R_Hand_Point001.transform.position;
    }
}