import Prefab_defen_01 from "view/raw/3d/Sprite3D_defen_01";
import X3D from "xengine/view/3d/X3D";
import { path } from "xengine/utils/path";
import Game from "xengine/game/Game";
import Trails from "../Trail";
import ColliderTrigger, { ColliderEvents } from "./ColliderTrigger";

// 具有运动轨迹 的对象
export default class TrailsSprite extends X3D.Sprite3D {
    trails: Trails; // 运动轨迹
    anchorNode: Laya.Sprite3D;
    // 实际模型节点 (可控制运动的模型)
    trailNode: Laya.Sprite3D;
    trigger: ColliderTrigger;

    constructor(url: string) {
        super(url);
        this.trails = new Trails();
    }

    async instance() {
        const node = await super.instance();
        this.anchorNode = this.displayObject;
        this.trailNode = this.anchorNode.getChildAt(0) as Laya.Sprite3D;
        this.trigger = this.trailNode.addComponent(ColliderTrigger);
        this.onEvent();
        return node;
    }

    onEvent() {
        this.trigger.eventer.on(ColliderEvents.TRIGGER_START, this, this.onTriggerEnter);
        this.trigger.eventer.on(ColliderEvents.COLLIDE_START, this, this.onCollisionEnter);
    }

    offEvent() {
        if (this.trigger) {
            this.trigger.eventer.off(ColliderEvents.TRIGGER_START, this, this.onTriggerEnter);
            this.trigger.eventer.off(ColliderEvents.COLLIDE_START, this, this.onCollisionEnter);
        }
    }

    dispose() {
        this.offEvent();
        this.trails && this.trails.dispose();
        this.trails = null;
        this.displayObject && this.displayObject.destroy();
        this.displayObject = null;
    }

    onTriggerEnter(event: any) {
    }


    onCollisionEnter(collision: Laya.Collision): void {
        console.log("碰撞体")
    }
}