import BallModule, { trailVo, baffleVo, ETrailType } from "modules/dataModule/BallModule";
import Game from "xengine/game/Game";
import TrailsSprite from "./base/TrailsSprite";

// 运动轨迹类
export default class Trails {
    trailsVo: trailVo[]; // 不动也属于运动
    private _pro: number; // 运行轨道 进度
    private _onlyNowPro: boolean; // 仅播放当前进度
    private _tween: Laya.Tween;
    anchorNode: Laya.Sprite3D;
    // 实际模型节点
    trailNode: Laya.Sprite3D;
    sprite3d: TrailsSprite;
    private _playing: boolean;

    private _nowPos: Laya.Vector3;
    private _nowRotate: Laya.Vector3;

    private _loop: boolean;
    private _timeMark: number = 10000;
    private _onceCallBack: Laya.Handler;

    constructor() {
        this._nowPos = new Laya.Vector3();
        this._nowRotate = new Laya.Vector3();
        this._tween = new Laya.Tween();
    }

    // 模型加载完后
    initData(trailsVo: trailVo[], sprite3d: TrailsSprite) {
        this.trailsVo = trailsVo;
        this.sprite3d = sprite3d;
        this.anchorNode = sprite3d.anchorNode;
        this.trailNode = sprite3d.trailNode;
        this._playing = false;
        this._pro = 0;
        this.updatePos();
    }

    /**
     * @param loop 是否循环
     * @param onceCallBack 结束回调
     * @param pro 播放制定进度
     */
    public startRun(loop: boolean = true, onceCallBack?: Laya.Handler, pro?: number) {
        this._loop = loop;
        this._pro = pro == null ? 0 : pro;
        this._onlyNowPro = pro == null ? false : true;
        this._onceCallBack = onceCallBack;
        this.playTrail();
    }

    public updatePos() {
        let trailVo = this.trailsVo[this._pro];
        if (trailVo) {
            this.anchorNode.transform.localPosition = trailVo.anchor.clone();
            this.trailNode.transform.localPosition = trailVo.startPos.clone();
        }
    }

    private playTrail() {
        if (!this.sprite3d.displayObject || this.sprite3d.displayObject.destroyed) return;
        this._playing = true;
        this.updatePos();
        let trailVo = this.trailsVo[this._pro];
        if (trailVo.trailData.type == ETrailType.LINE) {
            // 暂时认为 按表中的点 没有突发情况
            console.log("直线运动");
            let endPos = trailVo.endPos;
            this.trailNode.transform.localPosition.cloneTo(this._nowPos);
            this._tween.to(this._nowPos, {
                x: endPos.x, y: endPos.y,
                z: endPos.z, update: new Laya.Handler(this, () => {
                    this._nowPos.cloneTo(this.trailNode.transform.localPosition);
                    this.trailNode.transform.localPosition = this.trailNode.transform.localPosition;
                    // 暂时 实时更新 时间位置   LIUTODO
                    this.trailNode.transform.position = this.trailNode.transform.position;
                })
                // 暂时使用 这个完成回调 如果有误差 改成定时器
            }, this._timeMark / trailVo.trailData.speed, trailVo.ease, Laya.Handler.create(this, this.playTrailThen), null, true);
        }
        else if (trailVo.trailData.type == ETrailType.ROTATE) {
            console.log("曲线运动");
            let rotate = trailVo.rotate;
            this._tween.to(this._nowRotate, {
                x: rotate.x || 0, y: rotate.y || 0, z: rotate.z || 0, update: new Laya.Handler(this, () => {
                    this._nowRotate.cloneTo(this.anchorNode.transform.localRotationEuler);
                    this.anchorNode.transform.localRotationEuler = this.anchorNode.transform.localRotationEuler;
                })
            }, this._timeMark / trailVo.trailData.speed, trailVo.ease, Laya.Handler.create(this, this.playTrailThen), null, true)
        }
        else if (trailVo.trailData.type == ETrailType.CIRCLE) {
            this._circleAcher = this._circleAcher || new Laya.Sprite3D();
            let parent = this.sprite3d.displayObject.parent;
            parent.addChild(this._circleAcher);
            this._circleAcher.transform.localPosition = new Laya.Vector3(0, 0, 0);
            let index = this._circleIndex || new Laya.Sprite3D();
            this._circleAcher.addChild(index);
            index.transform.localPosition = this.trailNode.transform.localPosition;
            let rotate = trailVo.rotate;
            this._tween.to(this._nowRotate, {
                x: rotate.x || 0, y: rotate.y || 0, z: rotate.z || 0, update: new Laya.Handler(this, () => {
                    this._nowRotate.cloneTo(this.anchorNode.transform.localRotationEuler);
                    this.anchorNode.transform.localRotationEuler = this.anchorNode.transform.localRotationEuler;
                    this.trailNode.transform.position = this._circleIndex.transform.position;
                })
            }, this._timeMark / trailVo.trailData.speed, trailVo.ease, Laya.Handler.create(this, this.playTrailThen), null, true)
        }
        else {
            console.log("障碍物");
        }
    }
    private _circleAcher: Laya.Sprite3D;
    private _circleIndex: Laya.Sprite3D;

    private playTrailThen() {
        this._pro++;
        let callBack = () => {
            if (this._onceCallBack && this._onceCallBack.caller) {
                this._onceCallBack.run();
                this._onceCallBack = null;
            }
        }
        if (this._onlyNowPro) {
            callBack();
            return;
        }
        if (this._pro >= this.trailsVo.length) {
            if (!this._loop) {
                callBack();
                return;
            }
            this._pro = 0;
        }
        let delay = this.trailsVo[this._pro].trailData.pause || 0;
        if (!delay) this.playTrail();
        else Laya.timer.once(delay, this, this.playTrail);
    }

    public get Play() {
        return this._playing;
    }

    public set Play(bool: boolean) {
        this._playing = bool;
        if (bool)
            this.playTrail();
        else
            this.pause();
    }

    public dispose() {
        this._tween && this._tween.clear();
        this._tween = null;
    }

    public pause() {

    }
}