import { LevelVo } from "modules/dataModule/BallModule";
import { game_module } from "xengine/game/Module";
import Game from "xengine/game/Game";
import GameStaticData from "modules/dataModule/GameStaticData";
import UI_levelPar from "view/raw/Main/UI_levelPar";

/** This is an automatically generated class by FairyGUI. Please do not modify it. **/

export default class LevelPar extends UI_levelPar {

    public bar: fairygui.GImage;
    public barKey: fairygui.GImage;
    public levelNum: fairygui.GTextField;

    public smallLvData: LevelData[];
    private _lvNum: number;

    constructFromResource() {
        super.constructFromResource();
        this.bar = <fairygui.GImage><any>(this.getChild("bar"));
        this.barKey = <fairygui.GImage><any>(this.getChild("barKey"));
        this.levelNum = <fairygui.GTextField><any>(this.getChild("levelNum"));
    }

    setData(lvData: LevelData) {
        let gamedata = Game.inst.get_module<GameStaticData>(GameStaticData);
        this.smallLvData = gamedata.smallLevel_data[lvData.smallLv];
        let tempImage = this.barKey;
        this.barKey.visible = false;
        let len = this.smallLvData.length;
        let interval = 310 / len;
        this.levelNum.text = lvData.level.toString();

        this.value = 100 / len * this.smallLvData.indexOf(lvData);
        for (let i: number = 0; i < len - 1; i++) {
            let barKey = this.getChild("barkey" + i) || fairygui.UIPackage.createObjectFromURL(tempImage.resourceURL);
            barKey.x = 9 + interval * (i + 1);
            barKey.name = "barKey" + i;
            barKey.y = this.barKey.y;
            this.addChild(barKey);
            // 还有奖杯 和 钥匙
        }
    }
}