import BallModule, { baffleVo, boardVo } from "modules/dataModule/BallModule";
import { path } from "xengine/utils/path";
import Game from "xengine/game/Game";
import TrailsSprite from "./base/TrailsSprite";

// 篮板
export default class Board extends TrailsSprite {
    private _ballModule: BallModule;
    private _boardVo: boardVo;

    goalCube: Laya.Sprite3D;
    public skinName: string;

    initData(boardVo: boardVo) {
        this._ballModule = Game.inst.get_module<BallModule>(BallModule);
        this._boardVo = boardVo;
        this.skinName = boardVo.boardData.skin;
        this.url = path.res3DUrlByName(this._ballModule.futureResUrl, boardVo.boardData.skin, false);
    }

    async instance() {
        const node = await super.instance();
        this.goalCube = this.anchorNode.getChildByName("goalCube") as Laya.Sprite3D;
        return node;
    }

    // 游戏开始
    public startGame() {
        this.trails.initData(this._boardVo.trailVos, this);
        let physicsCollider = this.trailNode.getComponent(Laya.PhysicsCollider) as Laya.PhysicsCollider;
        physicsCollider && (physicsCollider.restitution = this._ballModule.boardForce);
        // 设置是否易碎
        // 打开碰撞
        // 设置位置
        this.trails.startRun();
    }
}