export class Parabola {
    private _endCallBack: Laya.Handler;
    private _progress: number = 0;
    public _target: Laya.Sprite3D;

    public forceDire: Laya.Vector3 = new Laya.Vector3();
    private _startTarget: Laya.Vector3;
    private _endTarget: Laya.Vector3;
    private _center: Laya.Vector3;
    private _speed: number = 0.02;
    private _targetRotationChange: boolean = true;

    private _startTime: number = 0;
    private _time: number = 0;

    private _xOffset: number = 0;

    public isLine: boolean = false; // 是否是直线

    // 帧率 来算
    public set speed(value: number) {
        this._speed = value;
    }

    // 时间 来算
    public set time(value: number) {
        this._time = value;
    }

    /**
     * @param dis 每帧跑的距离 (曲线只算一个近似的值) 会成为匀速
     */
    public speedByDis(dis: number) { // 根据距离算速度
        if (!this._startTarget || !this._endTarget || !this._center) {
            return;
        }
        let len1 = Laya.Vector3.distance(this._startTarget, this._endTarget);
        let len2 = Laya.Vector3.distance(this._startTarget, this._center) + Laya.Vector3.distance(this._endTarget, this._center);
        let len = (len1 + len2) / 2;
        this.speed = dis / len / 50;
    }

    private setCommon(target: Laya.Sprite3D, endCallBack: Laya.Handler) {
        this._endCallBack = endCallBack;
        this._target = target;
        this._progress = 0;
        Laya.timer.frameLoop(1, this, this.startTween);
    }

    public tweenParabolaComToCom(target: Laya.Sprite3D, startTarget: Laya.Vector3, endTarget: Laya.Vector3, endCallBack: Laya.Handler, center: Laya.Vector3, xOffset = 0) {
        this._startTime = Laya.Browser.now();
        this._startTarget = startTarget;
        this._endTarget = endTarget;
        this._center = center;
        this._xOffset = xOffset;
        this.setCommon(target, endCallBack);
    }

    public targetRotationChange(bool: boolean) {
        this._targetRotationChange = bool;
    }

    //t->(0,1)  stx:起始位置     kongzhiX：拉力点   endX ：终点
    private bezier(t: number, stx: number = 0, stY: number = 0, kongzhiX: number, kongzhiY: number, endX: number, endY: number): any {
        if (this.isLine)
            return { x: (endX - stx) * t + stx, y: (endY - stY) * t + stY };
        let tem = 1 - t;
        // let tx = tem * tem * stx + 2 * t * tem * kongzhiX + t * t * endX;
        let ty = tem * tem * stY + 2 * t * tem * kongzhiY + t * t * endY;
        let tz = tem * tem * stx + 2 * t * tem * kongzhiX + t * t * endX;
        return { y: ty, z: tz };//返回坐标位置
    }

    private bezierByVector3(t: number, startPos: Laya.Vector3, center: Laya.Vector3, endPos: Laya.Vector3) {
        let targetX = startPos.x + this._xOffset;
        let tx = startPos.x + (endPos.x - targetX) * t;
        let temp = this.bezier(t, startPos.z, startPos.y, center.z, center.y, endPos.z, endPos.y);
        temp.x = tx;
        return temp;
    }

    public dispose() {
        Laya.timer.clearAll(this);
        this._endCallBack = null;
        this._target = null;
        this._startTarget = null;
        this._endTarget = null;
        this._speed = 0.02;
        this._progress = 0;
    }

    private checkData(): boolean {
        if (!this._target || this._target.destroyed) {
            return true;
        }
        if (this._progress < 1 && this._target && this._startTarget && this._endTarget) {
            return true;
        }
        return false;
    }

    private startTween() {
        if (this._time) {
            this._progress = (Laya.Browser.now() - this._startTime) / this._time;
        }
        else {
            this._progress += this._speed;
        }
        if (!this.checkData()) {
            if (this._endCallBack) {
                this._endCallBack.run();
            }
        }
        else {
            this.updateTargetXY();
        }
    }

    private updateTargetXY() {
        let value = this._target;
        if (!value) return;
        let pList = this.bezierByVector3(this._progress, this._startTarget, this._center, this._endTarget);
        let xOff = value.transform.position.x - pList.x;
        let yOff = value.transform.position.y - pList.y;
        let zOff = value.transform.position.z - pList.z;
        // 按时间 算
        this.forceDire.x = -xOff;
        this.forceDire.y = -yOff;
        this.forceDire.z = -zOff;
        if (this._targetRotationChange) {
            let rotateZ = parseFloat((Math.atan2(yOff, - zOff) * 180 / Math.PI).toFixed(2));
            // console.log("y的旋转值是 ----------- " + rotateZ);
            // value.transform.rotate(new Laya.Vector3(0, 0, rotateZ), false, false);
            value.transform.rotationEuler.x = -rotateZ;
            value.transform.rotationEuler = value.transform.rotationEuler;
            // value.transform.localRotationEulerZ = -rotateZ;
        }
        value.transform.position.x = pList.x;
        value.transform.position.y = pList.y;
        value.transform.position.z = pList.z;
        value.transform.position = value.transform.position;
    }
}

export class ParabolaFunc {
    private _parabolas: Parabola[];
    private constructor() {
        this._parabolas = [];
    }
    private static _instance: ParabolaFunc;
    public static get instance() {
        ParabolaFunc._instance = ParabolaFunc._instance || new ParabolaFunc();
        return ParabolaFunc._instance;
    }

    /**
     * 曲线运动
     * @param target 目标
     * @param startTarget 
     * @param endTarget 
     * @param endCallBack 
     * @param center 中心点位置
     * @param ramdonRange 随机物体间隔范围 
     */
    public startPairingParabola(target: Laya.Sprite3D, startTarget: Laya.Vector3, endTarget: Laya.Vector3, endCallBack: Laya.Handler, center: Laya.Vector3, xOffset = 0): Parabola {
        let parabola = this.getParabola();
        parabola.tweenParabolaComToCom(target, startTarget, endTarget, Laya.Handler.create(this, () => {
            this.removeParabola(parabola);
            if (endCallBack && endCallBack.caller) {
                endCallBack.run();
                endCallBack = null;
            }
        }), center, xOffset);
        return parabola;
    }

    // 多一些的传法通过这个函数
    public getParabola(): Parabola {
        let parabola: Parabola = Laya.Pool.getItemByClass('parabola', Parabola);
        this._parabolas.push(parabola);
        return parabola;
    }

    //  请动作完成后置空缓动  此处移除 需要注意 已回收的物品不会重复回收
    public removeParabolaBySprite(item: Laya.Sprite3D) {
        this._parabolas.forEach((parabola: Parabola) => {
            if (parabola._target == item) {
                this.removeParabola(parabola);
            }
        })
    }

    //  请动作完成后置空缓动  此处移除 需要注意 已回收的物品不会重复回收
    public removeParabola(item: Parabola) {
        if (item && !item["__InPool"]) {
            item.dispose();
            var index: number = this._parabolas.indexOf(item);
            if (index >= 0) this._parabolas.splice(index, 1);
            Laya.Pool.recover('parabola', item);
            item = null;
        }
    }
}