import APIProvider, { RequetMethod, provider } from "../APIProvider";
import Game from "xengine/game/Game";
import WechatPlatform from "modules/platform/wechat/WechatPlatform";
import Platform from "modules/platform/platform";
import GameConfig from "config";

@provider("chongqing")
export default class ChongQingProvider extends APIProvider {
	readonly base_url: string = "https://games.api.gugudang.com/";
	readonly appid: string = "";
	openid: string = '';
	appkey: string = '';
	channel: string = "own";

	constructor(gameid: string) {
		super(gameid);
		this.appid = gameid;
		this.appkey = GameConfig.appkey;
	}

	async initialize() {
		const data = await this.request("get", "api/shares");
		this.share_info = {
			title: data.title,
			message: data.title,
			image_url: data.picurl,
			url: data.picurl,
			data
		};
		const p = Game.inst.get_module<Platform>(Platform);
		if (p instanceof WechatPlatform) {
			p.set_share_config(this.share_info);
			console.log("重庆API:", "拉取到分享配置", this.share_info);
		}

		try {
			const user = await Game.inst.get_module<Platform>(Platform).login();
			await this.login(user.uuid);
		} catch (error) {
			console.error("重庆API:", '登陆失败', error)
		}
	}

	async login(token: string) {
		let ret = await this.request("get", "api/login", {code: token, channel: 'own'});
		if (ret.code == 1) {
			console.log("重庆API:", "登陆成功", ret);
			this.openid = ret.openid;
			const p = Game.inst.get_module<Platform>(Platform);
			if (p instanceof WechatPlatform) {
				p.set_user_openid(this.openid);
			}
		} else {
			console.error("重庆API:", "登陆失败", ret);
		}
		return ret;
	}

	async request(method: RequetMethod, api: string, body?: object) {
		let new_body: object = {appid: this.appkey};
		if (typeof(body) === 'object') {
			new_body = {...new_body, ...body};
		}
		if (this.openid) {
			new_body = {
				...new_body,
				openid: this.openid
			};
		}
		return super.request(method, api, new_body);
	}

	/** 解析请求的相应结果 */
	protected parse_request_response(method: RequetMethod, api: string, url: string, response: any) {
		if (response.data) {
			return response.data;
		}
		return response;
	}

}

