import { EventDispatcher } from "xengine/events/EventDispatcher";

export enum ColliderEvents {
	TRIGGER_START = 'TRIGGER_START',
	COLLIDE_START = 'COLLIDE_START',
}

export default class ColliderTrigger extends Laya.Script3D {

	eventer = new EventDispatcher();

	onTriggerEnter(other: Laya.PhysicsComponent): void {
		this.eventer.event(ColliderEvents.TRIGGER_START, other);
	}

	onCollisionEnter(collision: Laya.Collision) {
		this.eventer.event(ColliderEvents.COLLIDE_START, collision);
	}
}
