import UI_Main from "view/raw/Main/UI_Main";
import XEngine from "xengine/XEngine";
import { FairyGUIBinder } from "view";
import TestDialog from "view/dialogs/TestDialog";
import { BallScene } from "view/3d/BallScene";
import LevelPar from "./LevelPar";
import BallModule from "modules/dataModule/BallModule";
import Game from "xengine/game/Game";
import UI_GameWinView from "view/raw/Main/UI_GameWinView";
import GameWinView from "./GameWinView";
import GameOverView from "./GameOverView";
import UI_GameOverView from "view/raw/Main/UI_GameOverView";
import MianView from "./MianView";
import ProView from "./ProView";
import UI_MainView from "view/raw/Main/UI_MainView";
import UI_ProView from "view/raw/Main/UI_ProView";

export default class MainScene extends UI_Main {
	static inst: MainScene = null;

	private _ballScene: BallScene;
	private _module: BallModule;

	public gameWinView: GameWinView = null;
	public gameFailView: GameOverView = null;
	public proView: ProView = null;
	public mianView: MianView = null;

	private allPanels = [];

	constructFromResource() {
		super.constructFromResource();
		MainScene.inst = this;
		this._module = Game.inst.get_module<BallModule>(BallModule);
		// FairyGUIBinder.onClick(this.m_bt_dialog, XEngine.inst.fairygui_helper, XEngine.inst.fairygui_helper.show_dialog, TestDialog, 'Dialog from MainScene');
		// FairyGUIBinder.onClick(this.m_bt_toast, XEngine.inst.fairygui_helper.toast, XEngine.inst.fairygui_helper.toast.popup, 'Toast from MainScene');
		this._ballScene = BallScene.inst;
		this.startGame();
	}

	startNextGame() {
		this._module.lvGoOn();
		this.startGame();
	}

	async startGame() {
		this.showMainView();
		this._ballScene.dispose();
		await this._ballScene.startGame();
		this.mianView.setData();
		this._ballScene.displayObject.active = true;
		this.displayObject.addChildAt(this._ballScene.displayObject, 0);
		this._module.preloadNextLv();
	}

	showGameWinView() {
		this.gameWinView = this.gameWinView || this.createObj(UI_GameWinView.URL, GameWinView);
		this.addChild(this.gameWinView);
		this.removeOtherPanel(this.gameWinView);
		return this.gameWinView;
	}

	showGameFailView() {
		this.gameFailView = this.gameFailView || this.createObj(UI_GameOverView.URL, GameOverView);
		this.addChild(this.gameFailView);
		this.removeOtherPanel(this.gameFailView);
		return this.gameFailView;
	}

	showProView() {
		this.proView = this.proView || this.createObj(UI_ProView.URL, ProView);
		this.proView.initPro();
		this.addChild(this.proView);
		this.removeOtherPanel(this.proView);
		return this.proView;
	}

	showMainView() {
		this.mianView = this.mianView || this.createObj(UI_MainView.URL, MianView);
		this.addChild(this.mianView);
		this.removeOtherPanel(this.mianView);
		return this.mianView;
	}

	parentRemove(com: any) {
		if (com && com.parent) {
			com.parent.removeChild(com);
		}
	}

	removeOtherPanel(com: any) {
		this.allPanels.forEach((view: any) => {
			if (view != com) {
				this.parentRemove(view);
			}
		})
	}

	private createObj(url, cls: any): any {
		let panel = fairygui.UIPackage.createObjectFromURL(url, cls) as any;
		this.allPanels.push(panel);
		return panel;
	}
}