import { game_module } from "xengine/game/Module";
import { StaticData } from "modules/common/StaticData";

@game_module('data')
export default class GameStaticData extends StaticData {
    baffle_data: baffleData[] = [];
    level_data: LevelData[] = [];
    config_data: configData[] = [];
    trail_data: trailData[] = [];
    config: any = null;

    levels_data: { [key: number]: LevelData } = {};
    smallLevel_data: { [key: number]: LevelData[] } = {}; // 关中关  每一关 有几个关卡

    protected get_res_member_map(): Object {
        return {
            "assets/data/json/Level.json": "level_data",
            "assets/data/json/baffle.json": "baffle_data",
            "assets/data/json/config.json": "config_data",
            "assets/data/json/trail.json": "trail_data",
            "assets/data/json/board.json": "board_data",
            "assets/data/json/hero.json": "hero_data",
            "assets/data/json/ball.json": "ball_data",
        };
    }

    protected on_config_loaded() {
        super.on_config_loaded();
        for (const lv of this.level_data) {
            this.levels_data[lv.level] = lv;
            if (!this.smallLevel_data[lv.smallLv]) {
                this.smallLevel_data[lv.smallLv] = [];
            }
            this.smallLevel_data[lv.smallLv].push(lv);
        }
    }

    /** 获取关卡配置 */
    get_level_data(level: number) {
        level = level || 0;
        return this.levels_data[level] || this.level_data[0];
    }
}