import X3D from "xengine/view/3d/X3D";
import XScene3DLaya from "xengine/view/3d/XScene3D.laya";
import BallModule, { LevelVo, baffleVo, heroVo } from "modules/dataModule/BallModule";
import Game from "xengine/game/Game";
import Scene_basketball from "view/raw/3d/Scene3D_basketball";
import Baffle from "./Baffle";
import { path } from "xengine/utils/path";
import Board from "./Board";
import Hero, { EHeroState } from "./Hero";
import { Ball } from "./Ball";
import { angleByPoint } from "xengine/utils/math";
import MainScene from "view/main/MainScene";
import Prefab_TouQiuLiHua from "view/raw/3d/Sprite3D_TouQiuLiHua";
import Prefab_Wo from "view/raw/3d/Sprite3D_Wo";
import Prefab_WanMei from "view/raw/3d/Sprite3D_WanMei";
import Prefab_HengBang from "view/raw/3d/Sprite3D_HengBang";
import Prefab_ChaoJiWanMei from "view/raw/3d/Sprite3D_ChaoJiWanMei";
import XSprite3D from "xengine/view/3d/XSprite3D.laya";
import { random_range } from "xengine/utils";
import Prefab_LuoDiYan from "view/raw/3d/Sprite3D_LuoDiYan";

export enum EEventType {
    PROCHANGE = "proChange",
    GAMEFAIL = "gameFail",
    GAMEWIN = "gameWin",
}

export class BallScene extends XScene3DLaya {
    private _ballModule: BallModule = null;
    private static _instance: BallScene;
    public camera: Laya.Camera;
    private _light: Laya.DirectionLight;
    private _baffles: Baffle[];
    // 现 只存在一个篮板
    public board: Board;
    public heros: Hero[];
    private _myHero: Hero;
    private _balls: Ball[];

    private _shootOverNum: number = 0;
    // 后退动作
    private _endOverNum: number = 0;

    private _gameOver: boolean = false;
    // 投篮算游戏开始
    private _touchStart: boolean = false;

    public levelVo: LevelVo;

    private constructor(url: string) {
        super(url);
        this._ballModule = Game.inst.get_module<BallModule>(BallModule);
    }

    static get inst() {
        this._instance = this._instance || new BallScene("");
        return this._instance;
    }

    async startGame() {
        this.levelVo = this._ballModule.nowLevelVo();
        this._shootOverNum = 0;
        this._endOverNum = 0;
        this._gameOver = false;
        this._touchStart = false;
        let skinName = this.levelVo.levelData.skin;
        this.url = path.res3DUrlByName(skinName, skinName);
        if (!this.displayObject || this.displayObject.url != this.url) {
            this.displayObject && this.displayObject.destroy();
            this.displayObject = await super.instance()
        }
        console.log("1");
        this.camera = this.displayObject.getChildByName("Camera") as Laya.Camera;
        this._light = this.displayObject.getChildByName("Light") as Laya.DirectionLight;
        this.addAllRestitution();
        this.initCamera();
        console.log("2");
        // let cls = Laya.ClassUtils.getClass(`ui.Scene_${skinName}`);
        // this.scene = new cls();
        // await this.scene.instance();
        // 生成篮板
        await this.createBoard();
        console.log("3");
        await this.createBaffles();
        console.log("4");
        await this.createHeros();
        console.log("5");
        this.add_child(this.board);
        this.board.startGame();
        // 生成挡板
        this.heros.forEach((hero: Hero, index: number) => {
            this.add_child(hero);
            hero.startGame();
        })
        this._baffles.forEach((baffle: Baffle, index: number) => {
            this.board.trailNode.addChild(baffle.displayObject);
            baffle.startGame();
        });
        this.startOnTouch();
        this.openCameraToHero(this._myHero.trailNode, true);
        this.initEffect();
        console.log("6");
    }

    // 下一轮投射开始
    public nextShootStart() {
        // 可能延迟 半秒 效果会好点
        this._shootOverNum = 0;
        this._endOverNum = 0;
        this.heros.forEach((hero: Hero, index: number) => {
            hero.startGame();
            hero.autoShoot();
        });
        this.startOnTouch();
    }

    private _lihua: Prefab_TouQiuLiHua;
    private _wo: Prefab_Wo;
    private _wanmei: Prefab_WanMei;
    private _hengbang: Prefab_HengBang;
    private _cwanmei: Prefab_ChaoJiWanMei;
    private _ballDisAni: Prefab_LuoDiYan;

    private _pingjias = [];

    private async initEffect() {
        this._lihua = await this.getPrefabBayClass(this._lihua, Prefab_TouQiuLiHua);
        this._wo = await this.getPrefabBayClass(this._wo, Prefab_Wo);
        this._wanmei = await this.getPrefabBayClass(this._wanmei, Prefab_WanMei);
        this._hengbang = await this.getPrefabBayClass(this._hengbang, Prefab_HengBang);
        this._cwanmei = await this.getPrefabBayClass(this._cwanmei, Prefab_ChaoJiWanMei);
        this._pingjias = [this._wo, this._wanmei, this._hengbang, this._cwanmei];
    }

    private async getPrefabBayClass(obj: XSprite3D, cls: any) {
        let temp: any = obj;
        if (!obj || obj.displayObject.destroyed) {
            temp = new cls();
            await temp.instance();
        }
        return temp;
    }

    public async playBallDis(pos: Laya.Vector3) {
        this._ballDisAni = await this.getPrefabBayClass(null, Prefab_LuoDiYan);
        this._ballDisAni.displayObject.transform.position = pos;
        let particleSystem = (this._ballDisAni.displayObject as Laya.ShuriKenParticle3D).particleSystem;
        this._ballDisAni.displayObject.active = true;
        this.displayObject.addChild(this._ballDisAni.displayObject);
        particleSystem.play();
    }

    public async playLihuaAndGood() {
        let pos = this.board.trailNode.transform.position;
        this._lihua.displayObject.transform.position = pos;
        let particleSystem = (this._lihua.displayObject as Laya.ShuriKenParticle3D).particleSystem;
        this._lihua.displayObject.active = true;
        this.displayObject.addChild(this._lihua.displayObject);
        particleSystem.play();
        let index = Math.floor(random_range(0, this._pingjias.length));
        let effect = this._pingjias[index];
        this.displayObject.addChild(effect.displayObject);
        effect.displayObject.transform.position = pos;
        effect.displayObject.active = true;
        effect.displayObject.particleSystem.play();
    }

    public shootProAdd() {
        this._shootOverNum++;
        if (this._shootOverNum >= this.heros.length) {
            // 投篮结束
            this.heros.forEach((hero: Hero, index: number) => {
                hero.shootEnd();
            })
        }
        this.event(EEventType.PROCHANGE);
    }

    public endProAdd() {
        this._endOverNum++;
        if (this._endOverNum >= this.heros.length) {
            // 下一轮开始
            this.nextShootStart();
        }
    }

    private _winHero: Hero;
    public gameOver(winHero: Hero) {
        // 播放一系列动画
        if (this._gameOver) {
            return;
        }
        this._winHero = winHero;
        this._gameOver = true;
        winHero.setHeroState(EHeroState.WIN);
        this.heros.forEach((hero: Hero) => {
            if (hero != winHero) {
                hero.setHeroState(EHeroState.FAIL);
            }
        })
    }

    public cameraToHeroAndResult() {
        let temp = new Laya.Vector3();
        Laya.Vector3.add(this._myHero.trailNode.transform.position, this._toHeroOffSet, temp);
        this.tweenCamera(temp, 1000, Laya.Handler.create(this, this.resultView));
    }

    // 打开结算界面
    public resultView() {
        if (this._winHero.heroVo.isPlayer) {
            // 玩家胜利
            MainScene.inst.showGameWinView();
            this.event(EEventType.GAMEWIN);
        }
        else {
            // 敌人胜利
            MainScene.inst.showGameFailView();
            this.event(EEventType.GAMEFAIL);
        }
    }

    public tweenCamera(point: Laya.Vector3, time: number, comCallBack?: Laya.Handler) {
        Laya.Tween.to(this.camera.transform.position, {
            x: point.x,
            y: point.y,
            z: point.z,
            update: new Laya.Handler(this, () => {
                this.camera.transform.position = this.camera.transform.position;
            })
        }, time, null, comCallBack);
    }

    public _toHeroOffSet: Laya.Vector3 = new Laya.Vector3(0, 1.7, -3.4);
    // 简单镜头跟随
    public openCameraToHero(com: Laya.Sprite3D, setHero: boolean = false) {
        com.addChild(this.camera);
        setHero && (this.camera.transform.localPosition = this._toHeroOffSet);
    }

    private addAllRestitution() {
        if (this.displayObject) {
            let childs = this.displayObject['_children'];
            if (childs) {
                childs.forEach((node: Laya.Sprite3D) => {
                    let physicsCollider = node.getComponent(Laya.PhysicsCollider) as Laya.PhysicsCollider;
                    physicsCollider && (physicsCollider.restitution = this._ballModule.panelForce);
                })
            }
        }
    }

    private closeCameraToHero() {
        this.camera.parent.removeChild(this.camera);
    }

    dispose() {
        this.heros && this.heros.forEach((hero: Hero, index: number) => {
            hero.dispose();
        })
        this.heros = [];
        this._balls && this._balls.forEach((ball: Ball) => {
            ball.dispose();
        })
        this._balls = [];
        this._baffles && this._baffles.forEach((baffle: Baffle) => {
            baffle.dispose();
        })
        this.board && this.board.dispose();
        this.board = null;
    }

    private startOnTouch() {
        Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
    }

    private _touchStartPos: any = new Laya.Point;
    private _touchMovePos: any = new Laya.Point;
    private onMouseDown(event: Laya.Event) {
        Laya.stage.on(Laya.Event.MOUSE_MOVE, this, this.onMouseMove);
        this._touchStartPos.x = event.stageX;
        this._touchStartPos.y = event.stageY;
    }

    private onMouseMove(event: Laya.Event) {
        this._touchMovePos.x = event.stageX;
        this._touchMovePos.y = event.stageY;
        let dis = this._touchMovePos.distance(this._touchStartPos.x, this._touchStartPos.y);
        // 判断是否可以弹射
        if (dis >= this._ballModule.touchDisShoot) {
            // 第一次开始
            if (!this._touchStart) {
                this._touchStart = true;
                MainScene.inst.showProView();
                // 视角切换
                let position = this.camera.transform.position;
                this.tweenCamera(new Laya.Vector3(position.x, 2.15, position.z), 1000);
                this.heros.forEach((hero: Hero) => {
                    hero.autoShoot();
                });
            }
            Laya.stage.off(Laya.Event.MOUSE_MOVE, this, this.onMouseMove);
            Laya.stage.off(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
            // 开始弹射
            let angle = angleByPoint(this._touchMovePos, this._touchStartPos);
            this._myHero.shoot(angle);
        }
    }

    private initCamera() {
        this.camera.enableRender = true;
        // this._camera.transform.position = new Laya.Vector3(0, 1, 10)
        let light = this._light;
        //灯光开启阴影
        light.active = true;
        light.shadow = true;
        //可见阴影距离
        light.shadowDistance = 20;
        //生成阴影贴图尺寸
        light.shadowResolution = 2048;
        // light.intensity = 5;
        //生成阴影贴图数量
        light.shadowPSSMCount = 1;
        //模糊等级,越大越高,更耗性能
        light.shadowPCFType = 3;
    }

    private async createBoard() {
        let boardVo = this.levelVo.boardVo;
        this.board = new Board("");
        this.board.initData(boardVo);
        await this.board.instance();
        // // 同一个 篮板  切换场景 暂时移除掉
        // if (this._board && this._board.skinName == boardVo.boardData.skin) {
        // }
    }

    private async createHeros() {
        let herosVo = this.levelVo.herosVo;
        let ballsData = this.levelVo.ballsData;
        let heroNum = herosVo.length;
        this.heros = [];
        for (let i: number = 0; i < heroNum; i++) {
            let temp = new Hero();
            this.heros[i] = temp;
            temp.initData(herosVo[i], ballsData[i], this);
            await temp.instance();
        }
        this._myHero = this.heros[0];
    }

    private async createBaffles() {
        this._baffles = [];
        let bafflesVo = this.levelVo.bafflesVo;
        let len = bafflesVo.length;
        for (let i: number = 0; i < len; i++) {
            let temp = new Baffle("")//Laya.Pool.getItemByClass("baffles", Baffle) as Baffle;
            this._baffles[i] = temp;
            temp.initData(bafflesVo[i]);
            await temp.instance();
        }
    }
}